// 
import { computed, onUnmounted, ref } from 'vue'
import dayjs from 'dayjs'
export const useCountDown = () => {
  // 1. 
  let timer = null
  const time = ref(0)
  //   xxxx
  const formatTime = computed(() => dayjs.unix(time.value).format('mmss'))
  // 2. 
  const start = (currentTime) => {
    // 
    // 1s
    time.value = currentTime
    timer = setInterval(() => {
      time.value--
    }, 1000)
  }
  // 
  onUnmounted(() => {
    timer && clearInterval(timer)
  })
  return {
    formatTime,
    start
  }
}