<script setup>
import { getOrderAPI } from '@/apis/pay'
import { onMounted, ref } from 'vue'
import { useRoute } from 'vue-router'
const route = useRoute()
const orderInfo = ref({})

const getOrderInfo = async () => {
    const res = await getOrderAPI(route.query.orderId)
    orderInfo.value = res.result
}

onMounted(() => getOrderInfo())

</script>


<template>
    <div class="xtx-pay-page">
        <div class="container">
            <!--  -->
            <div class="pay-result">
                <!--  -->
                <span class="iconfont icon-queren2 green" v-if="$route.query.payResult === 'true'"></span>
                <span class="iconfont icon-shanchu red" v-else></span>
                <p class="tit">{{ $route.query.payResult === 'true' ? '' : '' }}</p>
                <p class="tip"></p>
                <p><span></span></p>
                <p><span>{{ orderInfo.payMoney?.toFixed(2) }}</span></p>
                <div class="btn">
                    <el-button type="primary" style="margin-right:20px"></el-button>
                    <el-button></el-button>
                </div>
                <p class="alert">
                    <span class="iconfont icon-tip"></span>
                    
                </p>
            </div>
        </div>
    </div>
</template>

